  <template>
  <div class="board">
    <el-table
      :data="infosList"
      style="width: 100%">
      <el-table-column type="expand">
      <template slot-scope="props">
        <el-form label-position="left" inline class="demo-table-expand">
          <el-form-item label="公司名称">
            <span>{{ props.row.company_name }}</span>
          </el-form-item>
          <el-form-item label="公司标签">
            <span>{{ props.row.company_lables.join(" | ") }}</span>
          </el-form-item>
          <el-form-item label="公司规模">
            <span>{{ props.row.company_size }}</span>
          </el-form-item>
          <el-form-item label="技能要求">
            <span>{{ props.row.skill_lables.join(" | ") }}</span>
          </el-form-item>
          <el-form-item label="职位标签">
            <span>{{ props.row.position_lables.join(" | ") }}</span>
          </el-form-item>
          <el-form-item label="描述">
            <span>{{ props.row.position_advantage }}</span>
          </el-form-item>
          <el-form-item label="详细地址">
            <span>{{ props.row.linestaion }}</span>
          </el-form-item>
        </el-form>
      </template>
    </el-table-column>
      <el-table-column
        prop="position_name"
        label="职位名称"
        width="180">
      </el-table-column>
      <el-table-column
        prop="low_salary"
        label="低薪水(k)"
        width="180">
      </el-table-column>
      <el-table-column
        prop="high_salary"
        label="高薪水(k)">
      </el-table-column>
      <el-table-column
        prop="education"
        label="教育水平">
      </el-table-column>
      <el-table-column
        prop="work_year"
        label="工作年限">
      </el-table-column>
      <el-table-column
        prop="district"
        label="区域">
      </el-table-column>
    </el-table>
    <el-pagination
    background
    layout="prev, pager, next"
    :total="infosNum"
    :page-size="pageSize"
    @next-click="getInfos"
    @current-change="getInfos">
</el-pagination>
  </div>
  </template>

<script>
import axios from 'axios'

export default {
  data() {
    return {
      pageSize: 20,
      infosList:[],
      infosNum: 0,
      jobId: ''
    }
  },
  methods: {
    getInfos(page) {
      var host = location.hostname;
      var url = "http://" + host + ":3389/infos" + "?page=" + page + "&size=" + this.pageSize;
      if (this.jobId !== undefined) {
        url = url + '&job_id=' + this.jobId;
      }
      var datas = [];
      var num = 0;
      axios
        .get(url)
        .then(res => {
          datas = res.data.value;
          num = res.data.total;
          this.infosList = datas;
          this.infosNum = num;
        })
        .catch(err => {
          console.log(err)
        })
    },
    getParams() {
            this.jobId = this.$route.params.jobId
        }
  },
  created(){
    this.getParams();
    this.getInfos(1);
  }
}
</script>

<style>
  .text {
    font-size: 14px;
  }

  .item {
    padding: 18px 0;
  }

  .box-card {
    width: 480px;
  }
    .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>


